  --tw-gradient-to: rgb(255 253 235 / 0) var(--tw-gradient-to-position);
  --tw-gradient-stops: var(--tw-gradient-from), var(--tw-gradient-to);
}
.via-\[\#FFF1C7\]{
  --tw-gradient-to: rgb(255 241 199 / 0)  var(--tw-gradient-to-position);
  --tw-gradient-stops: var(--tw-gradient-from), #FFF1C7 var(--tw-gradient-via-position), var(--tw-gradient-to);
}
.to-\[\#FEF5C3\]{
  --tw-gradient-to: #FEF5C3 var(--tw-gradient-to-position);
}
.to-\[\#FFDC75\]{
  --tw-gradient-to: #FFDC75 var(--tw-gradient-to-position);
